body {
  min-width: 320px;
  margin: 0;
  padding: 0;

  font-size: 16px;
  line-height: 22px;
  font-family: "Arial", sans-serif;
  color: #ffffff;
  font-style: normal;
  
  background: url("../img/background.jpg") no-repeat;
  background-color: #101e2f;
  background-position: 50% 0%;
  background-size: cover;
  min-height: 100vh;
  background-attachment: fixed; //no scrolling
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
